* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country {
  grid-template: 60px auto / auto 1fr;
  align-items: center;
  gap: 20px;
  display: grid;
}

.country__name {
  grid-column: 2 / 3;
}

.country__list {
  grid-template: repeat(3, 1fr) / auto;
  grid-column: 1 / 3;
  display: grid;
}

.country__image {
  grid-area: 1 / 1 / 2 / 2;
}

.country__subtitle {
  font-size: 16px;
  font-weight: bold;
}

